* {
  box-sizing: border-box;
  margin: 0;
  font-family: Times, sans-serif;
}

my-button:not(:defined), my-footer:not(:defined), my-toolbar:not(:defined), my-double:not(:defined), my-hero-button:not(:defined), my-form:not(:defined) {
  opacity: 0;
}

my-button, my-toolbar, my-footer, my-double, my-hero-button, my-form {
  opacity: 1;
  transition: opacity 1s ease-in-out;
}

body {
  opacity: 1;
  background-color: #2c1c0c;
}

img {
  width: 100%;
}

section {
  opacity: 0;
  margin-top: 10vw;
}

section.loaded {
  opacity: 1;
  transition: opacity .4s ease-in-out;
}

.container {
  max-width: 1900px;
  width: 90%;
  background-color: #f8eeed;
  margin: 0 auto;
  overflow: hidden;
}

.hero-wrapper {
  padding: 4vw;
  overflow: hidden;
}

.hero-frame {
  overflow: hidden;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: scale .7s ease-in-out;
}

.hero-image:hover {
  scale: 1.015;
}

.video {
  padding: 4vw;
}

iframe {
  width: 80%;
  aspect-ratio: 16 / 9;
  margin: auto;
}

section iframe {
  filter: drop-shadow(0 0 10px #00000026) saturate() contrast(90%);
  mix-blend-mode: multiply;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 5vh;
  display: flex;
}

p:first-letter {
  color: red;
  font-weight: bold;
}

.testimonials {
  width: 70%;
  margin: auto;
  font-size: large;
}

.testimonials p {
  margin: 1vh auto;
  padding: 0 5vw;
  font-size: 1.3em;
  font-weight: 400;
}

@media only screen and (max-width: 480px) {
  .container {
    width: 100%;
    margin: 0 auto;
  }

  .hero-wrapper {
    width: 100%;
    padding: 4vw;
  }

  .hero-image {
    width: 100%;
    aspect-ratio: 5 / 7;
    object-fit: cover;
  }
}

@media only screen and (min-width: 481px) and (max-width: 768px) {
  .container {
    width: 100%;
    margin: 0 auto;
  }

  .hero-wrapper {
    width: 100%;
    padding: 4vw;
  }

  .hero-image {
    width: 100%;
    aspect-ratio: 5 / 7;
    object-fit: cover;
  }
}

@media only screen and (min-width: 769px) and (max-width: 1200px) {
  .container {
    width: 100%;
    margin: 0 auto;
  }

  p {
    letter-spacing: .05em;
    text-align: center;
    font-size: 1.3em;
    line-height: 1.04;
  }

  p:first-letter {
    color: #eb2821;
    font-size: 1.6em;
    font-weight: bold;
  }
}

@media only screen and (min-width: 1201px) {
  p {
    letter-spacing: .05em;
    text-align: center;
    text-rendering: geometricprecision;
    font-size: 1.7em;
    line-height: 1.04;
  }

  p:first-letter {
    color: #eb2821;
    font-size: 1.6em;
    font-weight: bold;
  }
}

